import {
  CategoriesQueryBuilder,
  Category,
  CategoryCreatedEnvelope,
  CategoryUpdatedEnvelope,
  UpdateCategory,
  createCategory as universalCreateCategory,
  getCategory as universalGetCategory,
  queryCategory as universalQueryCategory,
  updateCategory as universalUpdateCategory,
} from './comments-v1-category-categories.universal.js';

export const __metadata = { PACKAGE_NAME: '@wix/comments' };

export function createCategory(
  httpClient: HttpClient
): CreateCategorySignature {
  return (category: NonNullablePaths<Category, `name`, 2>) =>
    universalCreateCategory(
      category,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateCategorySignature {
  /**
   * Creates a new category.
   * @param - Category to create.
   * @returns Created category.
   */
  (category: NonNullablePaths<Category, `name`, 2>): Promise<
    NonNullablePaths<
      Category,
      | `reactionType`
      | `repliesReactionType`
      | `markedCommentLabelType`
      | `mainReaction.iconType`
      | `ratingsSettings.ratingsEnabled`
      | `ratingsSettings.ratingRequired`
      | `permissionsSettings.createComment.role`,
      4
    >
  >;
}

export function getCategory(httpClient: HttpClient): GetCategorySignature {
  return (categoryId: string) =>
    universalGetCategory(
      categoryId,
      // @ts-ignore
      { httpClient }
    );
}

interface GetCategorySignature {
  /**
   * Retrieves a category by ID.
   * @param - Category ID.
   * @returns Retrieved category.
   */
  (categoryId: string): Promise<
    NonNullablePaths<
      Category,
      | `reactionType`
      | `repliesReactionType`
      | `markedCommentLabelType`
      | `mainReaction.iconType`
      | `ratingsSettings.ratingsEnabled`
      | `ratingsSettings.ratingRequired`
      | `permissionsSettings.createComment.role`,
      4
    >
  >;
}

export function updateCategory(
  httpClient: HttpClient
): UpdateCategorySignature {
  return (
    _id: string,
    category: NonNullablePaths<UpdateCategory, `name` | `revision`, 2>
  ) =>
    universalUpdateCategory(
      _id,
      category,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateCategorySignature {
  /**
   * Updates a category.
   *
   * When you update `permissionsSettings`, existing comments remain visible even if they don't meet the updated permissions.
   *
   * Each time the category is updated, `revision` increments by 1.
   * The current `revision` must be passed when updating the category.
   * This ensures you're working with the latest category and prevents unintended overwrites.
   * @param - Category ID.
   * @returns Updated category.
   */
  (
    _id: string,
    category: NonNullablePaths<UpdateCategory, `name` | `revision`, 2>
  ): Promise<
    NonNullablePaths<
      Category,
      | `reactionType`
      | `repliesReactionType`
      | `markedCommentLabelType`
      | `mainReaction.iconType`
      | `ratingsSettings.ratingsEnabled`
      | `ratingsSettings.ratingRequired`
      | `permissionsSettings.createComment.role`,
      4
    >
  >;
}

export function queryCategory(httpClient: HttpClient): QueryCategorySignature {
  return () =>
    universalQueryCategory(
      // @ts-ignore
      { httpClient }
    );
}

interface QueryCategorySignature {
  /**
   * Creates a query to retrieve a list of comment categories.
   *
   * The `queryCategories()` method builds a query to retrieve a list of categories and returns a [`CategoriesQueryBuilder`](https://dev.wix.com/docs/sdk/backend-modules/comments/categories/categories-query-builder/ascending) object.
   *
   * The returned object contains the query definition, which is typically used to run the query using the [`find()`](https://dev.wix.com/docs/sdk/backend-modules/comments/categories/categories-query-builder/find) method.
   *
   * You can refine the query by chaining `CategoriesQueryBuilder` methods onto the query. `CategoriesQueryBuilder` methods enable you to sort, filter, and control the results that `queryCategories()` returns.
   *
   * The methods that are chained to `queryCategories()` are applied in the order they are called. For example, if you apply `ascending("voteSummary.netVoteCount")` and then `ascending("replyCount")`, the results are sorted first by the `"voteSummary.netVoteCount"`, and then, if there are multiple results with the same `"voteSummary.netVoteCount"`, the items are sorted by `"replyCount"`.
   *
   * The following `CategoriesQueryBuilder` methods are supported for the `queryCategories()` method. For a full description of the categories object, see the object returned for the [`items`](https://dev.wix.com/docs/sdk/backend-modules/comments/categories/categories-query-result/items) property in [`CategoriesQueryResult`](https://dev.wix.com/docs/sdk/backend-modules/comments/categories/categories-query-result/items).
   */
  (): CategoriesQueryBuilder;
}

export const onCategoryCreated = EventDefinition(
  'wix.comments.v1.category_created',
  true,
  (event: CategoryCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'entity.updatedDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<CategoryCreatedEnvelope>();
export const onCategoryUpdated = EventDefinition(
  'wix.comments.v1.category_updated',
  true,
  (event: CategoryUpdatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'entity.updatedDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<CategoryUpdatedEnvelope>();

